import { get } from "svelte/store";
import { cpuStore } from "../../store/state";
import { minimalAnimations, ttsEnabled } from "../../store/settings";
export function ZERO_FLAG_SET() {
    return cpuStore.get().zeroFlag.get();
}
export function NEGATIVE_FLAG_SET() {
    return cpuStore.get().negativeFlag.get();
}
export function MINIMAL_ANIMATIONS() {
    return get(minimalAnimations);
}
export function TTS_ENABLED() {
    return get(ttsEnabled);
}
export function NOT(condition) {
    return function () { return !condition(); };
}
export function AND(conditionA, conditionB) {
    return function () { return conditionA() && conditionB(); };
}
export function OR(conditionA, conditionB) {
    return function () { return conditionA() || conditionB(); };
}
