import App from "./App.svelte";
import Cpu from "./model/Cpu";
import Ram from "./model/Ram";
import SymbolTable from "./model/SymbolTable";
import { cpuStore, ramStore, symbolTableStore } from "./store/state";
import { init as initSettings } from "./store/settings";
import { init as initText } from "./store/text";
import ProgramExecution from "./execution/ProgramExecution";
import NonBlockingLoop from "./execution/NonBlockingLoop";
var ram = new Ram();
var cpu = new Cpu();
var symbolTable = new SymbolTable();
ramStore.set(ram);
cpuStore.set(cpu);
symbolTableStore.set(symbolTable);
symbolTable.addLabelEditedListener(function (event) { return ram.updateLabel(event.oldLabel, event.newLabel); });
symbolTable.addLabelRemovedListener(function (event) { return ram.removeLabel(event.removedLabel); });
var programExecution = new ProgramExecution(cpu);
var programExecutionLoop = new NonBlockingLoop(programExecution);
programExecutionLoop.start();
initSettings();
initText();
export default new App({
    target: document.body,
    props: {
        cpu: cpu,
        ram: ram,
        symbolTable: symbolTable,
        programExecution: programExecution
    }
});
